

/* --------------------------------------------------------------------------
   Product details page  ▸  /products/[slug]
   – Fully‑static, generated at build‑time (dynamicParams = false)
   – Elegant hero image, smooth Framer‑Motion entrance
   – Secondary gallery, related products shelf
   – Floating WhatsApp CTA pre‑filled with product title
--------------------------------------------------------------------------- */

import { Metadata } from 'next';
import { notFound } from 'next/navigation';
import Image from 'next/image';
import { MotionConfig, motion } from 'framer-motion';

import FloatingWhatsapp from '../../../components/FloatingWhatsapp';
import ProductCard from '../../../components/ProductCard';

import {
  allCategories,
  loadProducts,
  type Product,
} from '../../../lib/loaders.server';

// ──────────────────────────────────────────────────────────────────────────────
// Static‑site helpers
// ──────────────────────────────────────────────────────────────────────────────

/** Pre‑build a page for every product JSON record (SSG only). */
export const dynamicParams = false;
export async function generateStaticParams() {
  const params: { slug: string }[] = [];

  for (const cat of allCategories) {
    const products = await loadProducts(cat.slug);
    products.forEach((p) => params.push({ slug: p.slug }));
  }

  return params;
}

/** <title>, meta description and OG image for each product. */
export async function generateMetadata({
  params,
}: {
  params: { slug: string };
}): Promise<Metadata> {
  const product = await findProduct(params.slug);
  if (!product) return {};

  const mainImg =
    typeof product.img === 'string' ? product.img : product.img[0];

  return {
    title: `${product.title} | Crafting Corner`,
    description:
      product.description ??
      `Premium ${product.title} crafted for modern Indian homes.`,
    openGraph: {
      images: [mainImg],
    },
  };
}

/** Helper to locate a product by slug across all category JSON files. */
async function findProduct(slug: string): Promise<Product | null> {
  for (const cat of allCategories) {
    const products = await loadProducts(cat.slug);
    const found = products.find((p) => p.slug === slug);
    if (found) return found;
  }
  return null;
}

// ──────────────────────────────────────────────────────────────────────────────
// Page component
// ──────────────────────────────────────────────────────────────────────────────
export default async function ProductPage({
  params,
}: {
  params: { slug: string };
}) {
  const product = await findProduct(params.slug);
  if (!product) notFound();

  // ensure gallery is an array
  const gallery = Array.isArray(product.img) ? product.img : [product.img];

  // pull up to three "related" items from the same category
  const categoryProducts = await loadProducts(product.category);
  const related = categoryProducts
    .filter((p) => p.slug !== product.slug)
    .slice(0, 3);

  return (
    <MotionConfig transition={{ duration: 0.5, ease: 'easeOut' }}>
      <motion.main
        initial={{ opacity: 0, y: 24 }}
        animate={{ opacity: 1, y: 0 }}
        className="pb-32"
      >
        {/* ── Hero / main gallery image ─────────────────────────── */}
        <section className="relative mx-auto mb-12 grid max-w-6xl gap-6 px-4 lg:grid-cols-2">
          {/* Image */}
          <motion.figure
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="overflow-hidden rounded-2xl bg-walnut/5"
          >
            <Image
              src={gallery[0]}
              alt={product.title}
              width={900}
              height={700}
              className="h-full w-full object-cover object-center"
              priority
            />
          </motion.figure>

          {/* Details */}
          <motion.div
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            className="flex flex-col justify-center space-y-6"
          >
            <h1 className="font-display text-4xl font-semibold text-walnut">
              {product.title}
            </h1>

            <span className="inline-block w-max rounded-full bg-walnut/10 px-4 py-1.5 font-medium tracking-wide text-walnut">
              ₹{product.price.toLocaleString('en-IN')}
            </span>

            {product.description && (
              <p className="leading-relaxed text-charcoal/90">
                {product.description}
              </p>
            )}

            {product.tags?.length && (
              <ul className="flex flex-wrap gap-2">
                {product.tags.map((tag) => (
                  <li
                    key={tag}
                    className="rounded bg-charcoal/5 px-3 py-1 text-sm text-charcoal/80"
                  >
                    {tag}
                  </li>
                ))}
              </ul>
            )}
          </motion.div>
        </section>

        {/* ── Thumbnail gallery (if multiple images) ─────────────── */}
        {gallery.length > 1 && (
          <section className="mx-auto mb-20 grid max-w-5xl grid-cols-2 gap-4 px-4 md:grid-cols-3">
            {gallery.slice(1).map((img, i) => (
              <motion.div
                key={img}
                initial={{ opacity: 0, scale: 0.95 }}
                animate={{ opacity: 1, scale: 1 }}
                transition={{ delay: (i + 1) * 0.05 }}
                className="overflow-hidden rounded-xl bg-walnut/5"
              >
                <Image
                  src={img}
                  alt={`${product.title} alternate view ${i + 1}`}
                  width={600}
                  height={500}
                  className="h-full w-full object-cover object-center"
                />
              </motion.div>
            ))}
          </section>
        )}

        {/* ── Related products shelf ─────────────────────────────── */}
        {related.length > 0 && (
          <section className="mx-auto max-w-6xl px-4">
            <h2 className="mb-6 font-display text-2xl font-semibold text-walnut">
              You may also like
            </h2>

            <div className="grid grid-cols-[repeat(auto-fill,minmax(260px,1fr))] gap-8">
              {related.map((p) => (
                <ProductCard key={p.slug} {...p} />
              ))}
            </div>
          </section>
        )}

        {/* ── WhatsApp floating CTA ──────────────────────────────── */}
        <FloatingWhatsapp
          phone="+919999999999"
          message={`Hi! I'm interested in the ${product.title}. Could you tell me more?`}
        />
      </motion.main>
    </MotionConfig>
  );
}